<template>
  <div>
    <Spinner
      :class="{ 'fade-out': !isLoading }"
    />
    <div class="row shadow card profile__block mb-5">
      <img class="user__cover" loading="lazy" :src="profile.cover" />
      <UserDetail 
        :profile="profile"
        :orders="orders"
        :isLoading="isLoading"
      />
      <ProfileComments
        :comments="notRepeatComments"
        :likes="likes"
        :is-processing="isProcessing"
        :isLoading="isLoading"
      />
    </div>
  </div>
</template>

<script>
import UserDetail from '../components/UserDetail'
import ProfileComments from '../components/ProfileComments'
import Spinner from '../components/Spinner'
import usersAPI from '../apis/users'
import { Toast } from '../utils/helpers'

export default {
  name: 'Profile',
  components: {
    UserDetail,
    ProfileComments,
    Spinner
  },
  data() {
    return {
      profile: {
        id: '',
        account: '',
        name: '',
        email: '',
        password: '',
        phone: '',
        address: '',
        role: '',
        avatar: '',
        cover: '',
        introduction: '',
        createdAt: '',
        updatedAt: '',
        Comments: [],
      },
      likes: [],
      orders: 0,
      notRepeatComments: [],
      isProcessing: false,
      isLoading: true
    }
  },
  methods: {
    async fetchData(id) {
      try {
        this.isLoading = true
        const { data } = await usersAPI.getUser({ id })
        this.profile = data.profile
        this.likes = data.likes
        this.orders = data.orders.length
        this.notRepeatComments = data.notRepeatComments
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.isProcessing = false
        this.toastFire('error', err || '取得使用者資訊失敗，請檢查連線狀態或稍後再試')
      }
    },
    createComment(payload) {
      console.log('payload', payload)
      const { commentId, detailId, text } = payload
      this.Comments.push({
        id: commentId,
        text,
        productId: detailId,
        createdAt: new Date(),
        User: {
          id: this.currentUser.id,
          name: this.currentUser.name,
        },
      })
    },
    deleteComment(id) {
      this.Comments = this.Comments.filter(c => c.id !== id)
    },
    toastFire(action,text){
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false
      })
    }
  },
  beforeRouteUpdate(to, from, next) {
    const { id } = to.params
    this.fetchData(id)
    next()
  },
  created() {
    const { id } = this.$route.params
    this.fetchData(id)
  },
}
</script>

<style>
.user__cover {
  z-index: -1;
}
.profile__block {
  margin: 260px 0 0 0;
}
.profile__info__block .profile__nav__button:hover {
  background-color: #3a3a3a1c!important;
}
</style>
