<template>
  <div class="col-12 m-0 pl-0 pr-0 profile__info__block">
    <div id="profileNav" class="row profile__nav__block">
      <div
        class="btn profile__nav__button"
        :class="{ active: tabVisible === 'comments' }"
        @click="showTab('comments')"
      >
        評論過的商品
      </div>
      <div
        class="btn profile__nav__button"
        :class="{ active: tabVisible === 'likes' }"
        @click="showTab('likes')"
      >
        喜歡的商品
      </div>
    </div>

        <div 
        v-if="comments.length !== 0"
        class="row col-md-12 mt-4 mr-0 ml-0 mb-4 tab-pane fade"
        :class="{ show: tabVisible === 'comments' }"
        >
          <div
              v-for="comment in comments"
              :key="comment.id"
              class="col-md-6 mt-3 mb-3"
          >
              <div class="row">
              <div class="col-md-3 mr-3">
                  <router-link :to="{ name: 'products', params: { id: comment.ProductId } }">
                  <img
                      :src="comment.Product.image"
                      class="mr-3 img__box mxw__profile img-thumbnail shadow-sm lazy"
                  />
                  </router-link>
              </div>

              <div class="col-md-8 p-1">
                  <router-link :to="{ name: 'products', params: { id: comment.ProductId } }">
                  <h5 class="">{{ comment.Product.name }}</h5>
                  </router-link>

                  <span class="badge badge-pill badge-info">
                  {{ comment.Product.Category.name }}
                  </span>
                  <div class="mt-2 profile__info__description">
                  {{ comment.description }}
                  </div>
              </div>
              </div>
          </div>
        </div>

        <div
          v-else
          id="comments"
          class="row col-md-12 mt-4 mr-0 ml-0 mb-4 tab-pane fade"
          :class="{ show: tabVisible === 'comments' }"
        >
          <h3 class="col-12 text-center">尚未發表任何評論</h3>
        </div>

        <div 
            v-if="likes.length !== 0"
            class="row col-md-12 mt-4 mr-0 ml-0 mb-4 tab-pane fade"
            :class="{ show: tabVisible === 'likes' }"
        >
          <div v-for="like in likes" :key="like.id" class="col-md-6 mt-3 mb-3">
              <div class="row">
              <div class="col-md-3 mr-3">
                  <router-link :to="{ name: 'products', params: { id: like.ProductId } }">
                  <img
                      :src="like.Product.image"
                      class="mr-3 img__box mxw__profile img-thumbnail shadow-sm lazy"
                  />
                  </router-link>
              </div>

              <div class="col-md-8 p-1">
                  <router-link :to="{ name: 'products', params: { id: like.ProductId } }">
                  <h5 class="mt-0">{{like.Product.name}}</h5>
                  </router-link>

                  <span class="badge badge-pill badge-info">{{like.Product.Category.name}}</span>
                  <div class="mt-2 profile__info__description">
                      {{like.Product.description}}
                  </div>
                  <div class="btn btn-seco"></div>
              </div>
              </div>
          </div>
        </div>

        <div 
          v-else 
          class="row col-md-12 mt-4 mr-0 ml-0 mb-4 tab-pane fade"
          :class="{ show: tabVisible === 'likes' }"
        >
          <h3 class="col-12 text-center">目前沒有喜歡的商品</h3>
        </div>

  </div>
</template>

<script>
export default {
  name: 'UserDetailComments',
  props: {
    comments: {
      type: Array,
      required: true,
    },
    likes: {
      type: Array,
      required: true,
    }
  },
  data() {
    return {
        tabVisible: 'comments'
    }
  },
  methods: {
    showTab(tabName){
        this.tabVisible = tabName
    },
    addFavorite() {
      this.detail = {
        ...this.product,
        isFavorited: true,
      }
    },
    removeFavorite() {
      this.product = {
        ...this.product,
        isFavorited: false,
      }
    },
    addLike() {
      this.product = {
        ...this.product,
        isLiked: true,
      }
    },
    removeLike() {
      this.product = {
        ...this.product,
        isLiked: false,
      }
    },
  },
  created() {},
}
</script>

<style scoped>
 .tab-pane{
     display: none;
 }
  .tab-pane.show{
     display: flex;
 }
</style>