<template>
  <div class="row p-0 m-0 scoper">

    <div class="col-md-4 mt-3 profile__avatar">
      <figure class="figure">
        <img
          loading="lazy"
          :src="profile.avatar"
          class="figure-img img-fluid rounded img__box mxw__avatar rounded-circle"
        />
        <figcaption class="figure-caption"></figcaption>
      </figure>
    </div>

    <div v-if="currentUser.id === profile.id" class="profile__control__box row col-8 justify-content-end">
      <router-link
        :to="{
          name: 'editProfile',
          params: {
            id: profile.id,
          },
        }"
        class="btn btn-primary"
      >
        <i class="fas fa-pencil-alt text-white"></i>
        編輯個人檔案
      </router-link>
    </div>

    <div class="row p-3 mr-1 ml-1 col-8">
      <div class="col-md-12 mt-5">
        <h4 v-show="profile.name" class="col-md-12 mt-4 font-weight-bold">
          {{ profile.name }}
        </h4>

        <div v-show="profile.account" class="col-md-12 mt-3">
          <i class="fas fa-user-circle mr-1"></i>
          {{ profile.account }}
        </div>

        <div v-show="profile.email" class="col-md-12 mt-2">
          <i class="fas fa-envelope mr-1"></i>
          {{ profile.email }}
        </div>

        <div v-show="profile.phone" class="col-md-12 mt-2">
          <i class="fas fa-phone-alt mr-1"></i>
          {{ profile.phone }}
        </div>

        <div v-show="profile.address" class="col-md-12 mt-2">
          <i class="fas fa-map-marker-alt mr-2"></i>
          {{ profile.address }}
        </div>

        <div v-show="profile.introduction" class="col-md-12 mt-3">
          {{ profile.introduction }}
        </div>

        <div class="row m-0 mt-4 p-0 align-items-center">
          <i class="fas fa-trophy pl-3"></i>

          <div v-if="orders > 1" class="ml-3">
            已建立
            <strong class="text-info">
              {{ orders }}
            </strong>
            筆訂單
          </div>

          <div v-else class="ml-3">未建立任何訂單</div>

          <div v-if="profile.Comments.length > 1" class="ml-3">
            發表過
            <strong class="text-info">{{ profile.Comments.length }}</strong>
            則商品評價
          </div>

          <div v-else class="ml-3">尚無發表任何商品評論</div>
          
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'UserDetail',
  computed: {
    ...mapState(['currentUser'])
  },
  props: {
    profile: {
      type: Object,
      required: true,
    },
    orders: {
      type: Number,
      required: true,
    },
    isLoading: {
      type: Boolean,
      default: false
    }
  },
}
</script>

<style scoped>
.scoper {
  max-height: 350px;
  overflow: hidden;
}
</style>
